.main-header__toggle {
  position: relative;
  top: 14px;
  right: 0;
  left: 0;
  width: 16px;
  height: 24px;
  padding-left: 30px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle--close::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-header__toggle--open {
  position: relative;
  top: 9px;
  right: -2px;
  height: 24px;
  padding-top: 3px;
  padding-left: 30px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-header__toggle--open {
    display: none;
  }
}
.main-header__toggle--open::before, .main-header__toggle--open::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-header__toggle--open::before {
  transform: rotate(45deg);
}
.main-header__toggle--open::after {
  transform: rotate(-45deg);
}

.main-header__toggle:active {
  opacity: 30%;
}

.main-header--nojs {
  opacity: 0;
}
.main-header--nojs:active {
  opacity: 0;
}

